<template>
	<view class="menu-nav" :style="{ top: navigationBarHeight + 'px' }">
		<view class="menu-item" :class="{ active: modelValue === 0 }" @click="switchTab(0)">
			<text class="menu-text">寄快递</text>
		</view>
		<view class="menu-item" :class="{ active: modelValue === 1 }" @click="switchTab(1)">
			<text class="menu-text">寄大件</text>
		</view>
		<view class="menu-item" :class="{ active: modelValue === 2 }" @click="switchTab(2)">
			<text class="menu-text">网购退货</text>
		</view>
	</view>
</template>

<script>
export default {
	name: 'ExpressNav',
	props: {
		modelValue: {
			type: Number,
			default: 0
		},
		navigationBarHeight: {
			type: Number,
			default: 0
		}
	},
	emits: ['update:modelValue'],
	methods: {
		switchTab(index) {
			this.$emit('update:modelValue', index)
		}
	}
}
</script>

<style>
.menu-nav {
	position: fixed;
	left: 0;
	right: 0;
	z-index: 2;
	display: flex;
	justify-content: space-around;
	align-items: center;
	height: 44px;
	padding: 0 30rpx;
	backdrop-filter: blur(10px);
	-webkit-backdrop-filter: blur(10px);
}

.menu-item {
	position: relative;
	height: 44px;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 0 20rpx;
}

.menu-text {
	font-size: 30rpx;
	color: #262626;
	font-weight: normal;
}

.menu-item.active .menu-text {
	color: #262626;
	font-weight: bold;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.menu-item.active::after {
	content: '';
	position: absolute;
	bottom: 2px;
	left: 50%;
	transform: translateX(-50%);
	width: 40rpx;
	height: 3px;
	background: #007AFF;
	border-radius: 1.5px;
}
</style> 